.audio-controls {
    display: flex;
    justify-content: space-between;
    width:  480px;
    margin-top: 6px;
    margin-left: 0px;
    margin-right: 0px;
  }

  .nav
  {
    width: 70px;
    height: 60px;
    background-color:transparent;
    border: none;
    display: inline-block;
  }

  .nav:disabled
  {
    opacity: 0.3;
  }

  .control
  {
    width: 100px;
    height: 80px;
    background-color: transparent;
    border: none;
    display: inline-block;
  }
  
  .nav svg {
    width: 40px;
    height: 40px;
  }
  
  .control svg {
    height: 56px;
    width: 56px;
  }
  
